'use client'

import { useEffect, useState } from 'react'
import { motion } from 'framer-motion'

interface Candle {
  x: number
  y: number
  h: number
  w: number
  isUp: boolean
  opacity: number
}

export default function CandleStickBackground() {
  const [candles, setCandles] = useState<Candle[]>([])

  useEffect(() => {
    const generateCandles = () => {
      const newCandles: Candle[] = []
      const rows = 12
      const cols = 20
      
      for (let i = 0; i < cols; i++) {
        for (let j = 0; j < rows; j++) {
          // Add some randomness to positioning and presence
          if (Math.random() > 0.4) {
            const isUp = Math.random() > 0.5
            const height = 10 + Math.random() * 30
            const opacity = 0.03 + Math.random() * 0.07
            
            newCandles.push({
              x: (i * 100) / cols + (Math.random() * 2),
              y: (j * 100) / rows + (Math.random() * 5),
              h: height,
              w: 2,
              isUp,
              opacity
            })
          }
        }
      }
      setCandles(newCandles)
    }

    generateCandles()
  }, [])

  return (
    <div className="fixed inset-0 z-[-1] pointer-events-none overflow-hidden select-none">
      <svg width="100%" height="100%" className="opacity-40">
        {candles.map((candle, i) => (
          <g key={i} style={{ opacity: candle.opacity }}>
            {/* Wick */}
            <line
              x1={`${candle.x}%`}
              y1={`${candle.y - 5}%`}
              x2={`${candle.x}%`}
              y2={`${candle.y + candle.h + 5}%`}
              stroke={candle.isUp ? '#10b981' : '#ef4444'}
              strokeWidth="0.5"
            />
            {/* Body */}
            <rect
              x={`${candle.x - 0.5}%`}
              y={`${candle.y}%`}
              width="1%"
              height={`${candle.h}%`}
              fill={candle.isUp ? '#10b981' : '#ef4444'}
              rx="1"
            />
          </g>
        ))}
      </svg>
      
      {/* Overlay gradient to soften edges */}
      <div className="absolute inset-0 bg-gradient-to-b from-[#080B14] via-transparent to-[#080B14] opacity-60" />
      <div className="absolute inset-0 bg-gradient-to-r from-[#080B14] via-transparent to-[#080B14] opacity-60" />
    </div>
  )
}
